<template>
    <div>
        <div class="card centered"
             v-if="isLoadingResult">
            <app-pulse-loader></app-pulse-loader>
        </div>

        <div class="card background"
             v-if="!isLoadingResult">
            <template v-for="batch in getBatches">
                <div class="card bg-light mb-3">
                    <div class="card-header"
                         @click="batch.expand=!batch.expand"
                         style="cursor: pointer; font-size: x-large">
                        {{batch.batchResult.batchName}}
                    </div>
                    <div class="card-body" v-if="batch.expand">
                        <app-batch-result :batch-result="batch.batchResult"></app-batch-result>
                    </div>
                </div>

            </template>
        </div>
    </div>

</template>

<script>
    import BatchResult from './BatchResult';
    import PulseLoader from 'vue-spinner/src/PulseLoader.vue';
    import {mapGetters} from 'vuex';
    import {mapActions} from 'vuex';

    export default {
        name: 'Result',
        computed: mapGetters([
            "isLoadingResult",
            "getBatches"
        ]),
        components: {
            appBatchResult:BatchResult,
            appPulseLoader: PulseLoader
        },
        created() {
            if(this.isLoadingResult)
                this.fetchAllBatch();
        },
        methods: mapActions([
            "fetchAllBatch"
        ])
    }
</script>

<style scoped>

</style>
