<template>
    <div>
        <div class="card centered"
             v-if="isLoading">
            <app-pulse-loader></app-pulse-loader>
        </div>

        <div class="card background"
             v-if="!isLoading">
            <template v-for="chapter in getChapters">

                <div class="card bg-light mb-3">
                    <div class="card-header"
                         @click="chapter.expand=!chapter.expand"
                         style="font-family: 'Galada', cursive; cursor: pointer; font-size: xx-large">
                        {{chapter.name}}
                    </div>
                    <app-chapter-video v-if="chapter.expand"
                                       :chapter-video="chapter.chapterVideo"></app-chapter-video>
                </div>
            </template>
        </div>
    </div>
</template>

<script>
    import PulseLoader from 'vue-spinner/src/PulseLoader.vue';
    import ChapterVideo from "./ChapterVideo";
    import {mapGetters} from "vuex";
    import {mapActions} from "vuex";

    export default {
        name: 'Tutorial',
        computed: {
            ...mapGetters([
               "getChapters",
               "isLoading"
           ])
        },
        components: {
            appChapterVideo: ChapterVideo,
            appPulseLoader: PulseLoader
        },
        created() {
            if(this.isLoading){
                this.fetchAllChapters();
            }
        },
        methods: {
            ...mapActions([
               "fetchAllChapters"
           ])
        }
    }
</script>

