import axios from "axios";

const baseURL='https://spam-tutorial-result.firebaseio.com/';

export default {
    state: {
        tutorial: {
            chapters: [],
            loading: true
        }
    },
    getters: {
        getChapters(state){
            return state.tutorial.chapters;
        },

        isLoading(state){
            return state.tutorial.loading;
        }
    },
    mutations: {
        setChapters(state,chapters){
            state.tutorial.chapters=chapters;
            state.tutorial.loading=false;
        }
    },
    actions: {
        fetchAllChapters(context){
            let chapters=[];
            const link=baseURL+"chapters.json";
            //console.log(link);
            axios.get(link)
                .then(res=>{
                    for(let key in res.data){
                        let chapter={
                            name: '',
                            expand: false,
                            chapterVideo: {
                                key,
                                loading: true,
                                videos: [],
                                newVideos: []
                            }
                        };
                        chapter.name=res.data[key].chapter;

                        chapters.push(chapter);
                    }
                    context.commit('setChapters',chapters);
                    //console.log(this.chapters);
                }).catch(error=>{
                //console.log(error);
            });
        },

        fetchAllVideo(context,chapter) {
            chapter.videos = [];
            chapter.newVideos = [];

            const link = baseURL + chapter.key + ".json";

            axios.get(link)
                .then(res => {

                    for (let key in res.data) {
                        let video = {
                            videoTitle: '',
                            videoLink: '',
                            downloadLink: ''
                        };

                        video.videoTitle = res.data[key].videoTitle;
                        video.videoLink = res.data[key].videoLink;
                        video.downloadLink = res.data[key].downloadLink;

                        chapter.videos.push(video);

                        let d = new Date(res.data[key].alertDate);
                        if (d > (new Date()))
                            chapter.newVideos.push(video);
                    }

                    chapter.loading = false;
                }).catch(error => {
                //console.log(error);
            });
        }
    }
};
