<template>
    <div>
        <div class="card centered"
             v-if="isLoading">
            <app-pulse-loader></app-pulse-loader>
        </div>
        <div class="card  background"
             v-if="!isLoading">

            <!--      New Videos -->
            <template v-if="chapterVideo.newVideos.length>0">

                <div class="alert alert-warning"
                     style="cursor: pointer">
                    <div class="alert-heading">
                        <strong style="font-size: x-large">!!!New Tutorial</strong>
                        <app-video v-for="video in chapterVideo.newVideos"
                                   :video="video"></app-video>
                    </div>
                </div>

            </template>

            <!--      Old Videos-->
            <template v-if="chapterVideo.videos.length>0">

                <div class="card bg-light mb-3">
                    <div class="card-body">
                        <app-video v-for="video in chapterVideo.videos"
                                   :video="video"></app-video>
                    </div>
                </div>

            </template>
        </div>
    </div>
</template>

<script>
    import PulseLoader from 'vue-spinner/src/PulseLoader.vue';
    import Video from './Video';
    import {mapActions} from 'vuex';

    export default {
        name: 'ChapterVideo',
        props: ['chapterVideo'],
        computed: {
            isLoading(){
                return this.chapterVideo.loading;
            }
        },
        components: {
            appVideo:Video,
            appPulseLoader:PulseLoader
        },
        created() {
            if(this.isLoading)
                this.fetchAllVideo(this.chapterVideo);
        },
        methods: mapActions([
            "fetchAllVideo"
        ])
    }
</script>

