import axios from 'axios';

const baseURL='https://spam-tutorial-result.firebaseio.com/';

export default {
    state: {
        results: {
            loading: true,
            batches: []
        }
    },
    getters: {
        isLoadingResult(state){
            return state.results.loading;
        },

        getBatches(state){
            return state.results.batches;
        }
    },
    mutations: {
        setBatches(state,batches){
            state.results.batches=batches;
            state.results.loading=false;
        }
    },
    actions: {
        fetchAllBatch(context){
            let batches=[];
            const link=baseURL+"years.json";
            //console.log(link);
            axios.get(link)
                .then(res=>{
                    for(let key in res.data){
                        let batch={
                            expand: false,
                            batchResult: {
                                batchName: '',
                                loading: true,
                                results: [],
                                recentResults: []
                            }
                        };
                        batch.batchResult.batchName=res.data[key].year;

                        batches.push(batch);
                    }

                    batches=batches.reverse();

                    context.commit("setBatches",batches);
                    //console.log(this.chapters);
                }).catch(error=>{
                //console.log(error);
            });
        },

        fetchAllResult(context,batchResult){
            batchResult.results=[];
            batchResult.recentResults=[];

            const link=baseURL+batchResult.batchName+".json";

            axios.get(link)
                .then(res=>{
                    for(let key in res.data){
                        let result={
                            resultTitle: '',
                            downloadLink: ''
                        };
                        result.resultTitle=res.data[key].resultTitle;
                        result.downloadLink=res.data[key].downloadLink;
                        batchResult.results.push(result);

                        let d=new Date(res.data[key].alertDate);
                        if(d>(new Date()))
                            batchResult.recentResults.push(result);
                    }
                    batchResult.loading=false;
                }).catch(error=>{
                //console.log(error);
            });
        }
    }
};
